class Runner {
    constructor(Task, ...args) {
        this._processing = false;
        this.Task = Task;
        this.args = args;
    }
    /**
     * Schedule a task
     * @param callback Callback function
     * @returns {void}
     */
    /* eslint-disable-next-line @typescript-eslint/no-unused-vars */
    schedule(callback) {
        // placeholder
    }
    /**
     * Cancel the task
     * @returns {void}
     */
    cancel() {
        if (this.task) {
            this.task.cancel();
        }
    }
    /**
     * Immediately fulfil the task
     * @returns {void}
     */
    fulfil() {
        if (this.task) {
            this.task.fulfil();
        }
    }
    /**
     * Initialise a runner task
     * @param callback Task callback function
     * @returns {void}
     */
    createTask(callback) {
        this._task = new this.Task(callback, ...this.args);
    }
    /**
     * Run the main callback
     * @param callback The callback to execute
     * @returns {void}
     */
    runCallback(callback) {
        this._processing = true;
        try {
            callback();
        }
        catch (e) {
            setTimeout(() => {
                throw e;
            });
        }
        finally {
            this._processing = false;
        }
    }
    /**
     * Checks to see whether the
     * runner is currently processing the task
     */
    get processing() {
        return this._processing;
    }
    /**
     * Checks to see whether the
     * runner is currently processing the task
     */
    get task() {
        return this._task;
    }
}
export { Runner };
