let Tasks = new Set();
/**
 * Force run all pending tasks
 * Useful for testing purposes
 * @returns {void}
 */
const flush = () => {
    const tasks = Tasks;
    Tasks = new Set();
    for (const task of tasks) {
        task.fulfil();
    }
};
/**
 * A utility class to create Task
 */
class Task {
    /**
     * Create the new task
     * @param callback The callback to execute
     */
    constructor(callback, ...args) {
        this.callback = callback;
        this.args = args;
        Tasks.add(this);
    }
    /**
     * Immediately fulfil the callback
     * @returns {void}
     */
    fulfil() {
        if (Tasks.has(this)) {
            Tasks.delete(this);
            this.callback();
        }
    }
    /**
     * Cancel the task
     * @returns {void}
     */
    cancel() {
        Tasks.delete(this);
    }
}
export { Task, flush };
